import { Component, OnInit, AfterViewChecked, ViewChild, ElementRef, ViewChildren, Directive, QueryList } from '@angular/core';
import { D3Service } from '../d3.service';
import { LayoutManager } from '../graph/layout-manager';
import { AreaService } from '../area.service';
import { EduStructureService } from '../edu-structure.service';
import { RecommendService } from '../recommend.service';
import { Rect } from '../graph/rect';
import { JobInfo } from '../job-info';
import { Constants } from '../constants';
import { StateService } from '../state.service';
import { trigger, style, transition, animate } from '@angular/animations';


@Component({
  selector: 'app-main-graph',
  templateUrl: './main-graph.component.html',
  styleUrls: ['./main-graph.component.css'],
  animations: [
    trigger('initAnimation', [
      transition('void => init', [
        animate('0.3s ' + "{{delay}}ms", style({ transform: 'scale(1.0, 1.1)', backgroundColor: '#749dd8' })),
        animate('0.3s 0.3s', style({ transform: 'scale(1.0)', backgroundColor: '#fff' })),
      ], { params: { delay: 0 } }),
      // transition('void => done', [
      //   animate('0.3s', style({ transform: 'scale(1.0, 1.1)', backgroundColor: '#749dd8' })),
      //   animate('0.3s', style({ transform: 'scale(1.0)', backgroundColor: '#fff' })),
      // ], { params: { delay: 0 } }),
      transition('init => done', [
        animate(0, style({ backgroundColor: '#fff' }))
      ])
    ]),
  ]
})
export class MainGraphComponent implements OnInit, AfterViewChecked {

  private jobInfos: JobInfo[];
  private currJobIndex: number;
  private currPathIndex: number;
  private selectedRect: Rect;

  @ViewChild('svg') svg: ElementRef;
  layoutManager: LayoutManager;
  currState: number = StateService.INFO;
  initState = 'init';
  initAnimationCount = 0;
  

  constructor(public d3Service: D3Service,
    private areaService: AreaService,
    private eduService: EduStructureService,
    private stateService: StateService,
    private recommendService: RecommendService) {
  }

  ngOnInit() {
    this.layoutManager = this.d3Service.layoutManager;
    this.d3Service.applySVG(this.svg, Constants.GRAPH_WIDTH, Constants.GRAPH_HEIGHT);
    this.d3Service.initYAxis();
  }

  ngAfterViewInit() {
    this.areaService.areaObservable.subscribe(area => {
      this.eduService.applyArea(area);
      this.layoutManager.updateRects(this.currState >= StateService.DETAIL);
      this.d3Service.updateLinks();
    });

    this.stateService.stateObservable.subscribe(state => {
      if (this.currState == StateService.OVERVIEW && state == StateService.DETAIL) {
        this.d3Service.changeDetailMode(this.jobInfos[this.currJobIndex], this.currPathIndex);
      } else if (this.currState == StateService.DETAIL && state == StateService.OVERVIEW) {
        this.d3Service.changeOverviewMode(this.jobInfos[this.currJobIndex].paths, this.currPathIndex);
      }
      this.selectedRect = null;
      this.currState = state;
    })


    this.recommendService.recommendJobInfoObservable.subscribe(jobInfos => {
      this.jobInfos = jobInfos;
      this.currJobIndex = 0;
      this.currPathIndex = 0;
      this.d3Service.deleteLinks();
      this.d3Service.initLinks(jobInfos[this.currJobIndex].paths);
      this.d3Service.highlightLinks(this.currPathIndex);
    });

    this.recommendService.currJobIndexObservable.subscribe(jobIndex => {
      this.currJobIndex = jobIndex;
      this.currPathIndex = 0;
      this.d3Service.deleteLinks();
      this.d3Service.initLinks(this.jobInfos[jobIndex].paths);
      this.d3Service.highlightLinks(this.currPathIndex);
    });

    this.recommendService.currPathIndexObservable.subscribe(pathIndex => {
      this.currPathIndex = pathIndex;
      this.d3Service.highlightLinks(pathIndex);
    })
  }

  showInfo(selectedRect: Rect) {
    this.selectedRect = selectedRect;
    if (this.currState == StateService.INFO) {
      this.eduService.selectCourseNode(selectedRect.courseNode);
    } else if (this.currState == StateService.DETAIL) {
      //   
      if (selectedRect.courseNode.layer == 4) {
        this.stateService.changeState(StateService.MAJOR);
        //   
      } else {
        this.stateService.changeState(StateService.SCHOOL);
      }
    }
  }

  ngAfterViewChecked() {
  }

  selectPath(index: number) {
    this.recommendService.changePathIndex(index);
  }

  highlightPath(index: number) {
    this.d3Service.highlightLinks(index);
  }

  goToDetailMode() {
    this.stateService.changeState(StateService.DETAIL);
  }

  getFlexFont(rect: Rect) {
    let maxWidth = 0;
    switch (this.currState) {
      case StateService.INFO:
        maxWidth = Math.min(rect.width, 300);
        return '' + (maxWidth / 280);
      default:
        maxWidth = Math.min(rect.width, 250);
        return '' + (maxWidth / 300);
    }
  }

  initAnimationDone() {
    this.initAnimationCount++;
    if (this.initAnimationCount == this.layoutManager.totalRectCount) {
      this.initState = 'done';
    }
  }
}
